import React, { useEffect, useState } from "react";
import './Description.css';
import { useParams } from "react-router-dom";

export default function description(props) {

  const [currentMovieDetail, setMovie] = useState()
  const { id } = useParams()


  useEffect(() => {

    function fetchdata() {

      fetch(`http://api.themoviedb.org/3/movie/${id}?api_key=4e44d9029b1270a757cddc766a1bcb63&language=en-US`).then((response) => {
        console.log(response);
        return response.json();

      })
        .then((data) => {
          console.log(data);
          setMovie(data);
        })
        .catch((error) => {
          console.log("we are facing a error");
        })

    }
    fetchdata();
  }, [id]);

  return (
    <div className="movie">
      <div className="movie__intro">
        <img className="movie__backdrop" src={`https://image.tmdb.org/t/p/original${currentMovieDetail ? currentMovieDetail.backdrop_path : ""}`} alt="Nothing is present"/>
      </div>
      <div className="movie__detail">
        <div className="movie__detailLeft">
          <div className="movie__posterBox">
            <img className="movie__poster" src={`https://image.tmdb.org/t/p/original${currentMovieDetail ? currentMovieDetail.poster_path : ""}`} alt="Nothing is present"/>
          </div>
        </div>
        <div className="movie__detailRight">
          <div className="movie__detailRightTop">
            <div className="movie__name">{currentMovieDetail ? currentMovieDetail.original_title : ""}</div>
            <div className="movie__tagline">{currentMovieDetail ? currentMovieDetail.tagline : ""}</div>
            <div className="movie__rating">
              {currentMovieDetail ? currentMovieDetail.vote_average : ""} <i class="fas fa-star" />
              <span className="movie__voteCount">{currentMovieDetail ? "(" + currentMovieDetail.vote_count + ") votes" : ""}</span>
            </div>
            <div className="movie__runtime">{currentMovieDetail ? currentMovieDetail.runtime + " mins" : ""}</div>
            <div className="movie__releaseDate">{currentMovieDetail ? "Release date: " + currentMovieDetail.release_date : ""}</div>
            <div className="movie__genres">
              {
                currentMovieDetail && currentMovieDetail.genres
                  ?
                  currentMovieDetail.genres.map(genre => (
                    <><span className="movie__genre" id={genre.id}>{genre.name}</span></>
                  ))
                  :
                  ""
              }
            </div>
          </div>
          <div className="movie__detailRightBottom">
            <div className="synopsisText">Synopsis</div>
            <div>{currentMovieDetail ? currentMovieDetail.overview : ""}</div>
            <div className="description_buttons">

            {
              currentMovieDetail && currentMovieDetail.homepage && <a href={currentMovieDetail.homepage} target="_blank" rel="noopener noreferrer" style={{ textDecoration: "none" }}><p><span className="movie__homeButton movie__Button">Watch Now <i className="newTab fas fa-external-link-alt"></i></span></p></a>
            }
            {
              currentMovieDetail && currentMovieDetail.imdb_id && <a href={"https://www.imdb.com/title/" + currentMovieDetail.imdb_id} target="_blank" rel="noopener noreferrer" style={{ textDecoration: "none" }}><p><span className="movie__imdbButton movie__Button">Watch Trailer<i className="newTab fas fa-external-link-alt"></i></span></p></a>
            }

            </div>
          </div>

        </div>
      </div>

      <div className="footer">


        <div className="movie__heading" style={{ color: "white",fontStyle:"italic" }}>Production companies</div>
        <div className="movie__production">
          {
            currentMovieDetail && currentMovieDetail.production_companies && currentMovieDetail.production_companies.map(company => (
              <>
                {
                  company.logo_path
                  &&
                  <span className="productionCompanyImage">
                    <img className="movie__productionComapany" src={"https://image.tmdb.org/t/p/original" + company.logo_path} alt="Nothing is present"/>
                    <span>{company.name}</span>
                  </span>
                }
              </>
            ))
          }
        </div>
      </div>

    </div>
  )
}
